/* eslint-disable */
import react from 'react'
import { View } from '@tarojs/components'
import { Cell, Popup, PopupProps } from '@antmjs/vantui'

export default function Demo() {
  const [show, setShow] = react.useState(false)
  const [position, setPosition] = react.useState<PopupProps['position']>('top')

  const showAction = function (position) {
    setPosition(position)
    setShow(true)
  }

  return (
    <View>
      <Cell title="" isLink onClick={() => showAction('top')} />
      <Cell title="" isLink onClick={() => showAction('bottom')} />
      <Cell title="" isLink onClick={() => showAction('left')} />
      <Cell title="" isLink onClick={() => showAction('right')} />
      <Popup show={show} position={position} onClose={() => setShow(false)}>
        {position}
      </Popup>
    </View>
  )
}
